* {
    list-style: none;
  }

.book-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80%;
    max-width: 800px;
  }
  
  .book-card {
    border: 2px solid hsl(0, 41%, 3%);
    padding: 20px;
    margin-bottom: 20px;
    width: 200px;
    text-align: center;
  }
  
  .book-card h3 {
    margin-bottom: 10px;
  }
  
  .book-card p {
    margin-bottom: 10px;
  }
  
  button {
    background-color: #4CAF50;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
  }

  .search-header {
    display: inline-block;
    justify-content: space-between; 
    align-items: center; 
    margin-bottom: 20px;
  }
  
  .search-header h1 {
    display: inline-block;
  }

  .search-header nav {
    display: inline-block;
    justify-content: space-between;
    width: 80%;
    max-width: 800px;
    margin-bottom: 20px;
  }
  
 nav a {
    padding: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    cursor: pointer;
  }