  body {
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  h1 {
    margin-bottom: 20px;
  }
  
  .book-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 80%; 
    max-width: 800px; 
    border: 1px solid hsl(0, 38%, 3%);
    border-radius: 2px;
    margin-bottom: 20px;
  }
  
  .book-card {
    padding: 20px; 
    margin-bottom: 20px; 
    width: 200px; 
    text-align: center;
  }
  